<template>
<div class="block">
	<div class="intro">
		<img src="@/assets/tx.png" />
		<div class="introtext">
			路 - 自由职业者 (无业)
			<div>
				<el-tag>懒癌晚期</el-tag>
				<el-tag type="success">直男癌</el-tag>
				<el-tag type="info">二次元</el-tag>
				<el-tag type="warning">宅男</el-tag>
				<el-tag type="danger">矮穷挫</el-tag>
			</div>
		</div>
	</div>
	
  <el-timeline  v-loading="loading">
    <el-timeline-item v-for="(d,index) in date" :key="index" :timestamp="d.date" type="primary " placement="top"  >
      <el-card>
        <h4 class="title">{{d.title}}</h4>
        <p class="text">{{d.text}}</p>
      </el-card>
		</el-timeline-item>

  </el-timeline>
</div>
</template>



<script>
export default {
  name: 'about',
  data () {
    return {
			loading:true,
      date:[]
    }
  },
			created(){
				this.$axios.get('/MyWeb/intro')
				.then(res=>{
					if(res.data.code==0){
							this.date=res.data.data;
							this.loading=false;
					}
				})
				.catch(error=>{
					console.log(error);
				})
		  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>


.block{
	margin: 20px auto;
	max-width: 900px;
	.intro{
		text-align: center;
		.introtext{
			font-size:1.5em;
			line-height: 1.5em;
		}
	}
	.title{
		font-weight: 500;
		color: #000000;
			padding:1.33em;
			padding-left: 0;
			font-size: 1.2em;
	}
	.text{
		line-height: 2em;
	}
	
	
}




</style>



