<template>
	<div class="blogs">
		<div class="lable">
			<el-tag  v-for="(i,index) in lables" :key="index" @click="lablesactive(index)" :class="count==index ? 'lablecolor' : '0'" >{{i}}</el-tag>
		</div>
		<div class="box">
			<div class="box-box">
				<div class="box-lable">Java</div>
				<img class="box-img" align="bottom" src="../../../static/cs.jpg" />
				<div class="box-text">
					<h3>IO 流</h3>
				</div>
			</div>
		</div>
		<div class="box">
			<div class="box-box">
				<div class="box-lable">Java</div>
				<img class="box-img" align="bottom" src="../../../static/cs.jpg" />
				<div class="box-text">
					<h3>IO 流</h3>
				</div>
			</div>
		</div>
		<div class="box">
			<div class="box-box">
				<div class="box-lable">Java</div>
				<img class="box-img" align="bottom" src="../../../static/cs.jpg" />
				<div class="box-text">
					<h3>IO 流</h3>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	export default {
	  name: 'blog',
	  data () {
	    return {
	      lables:["全部","Java","JavaScript","MySQL","HTMLCSS","杂记"],
		  count:"0",
	    }
	  },
	  methods: {
	  	lablesactive(event) {
	  		this.count=  event==this.count ? "0" : event ;
	  	}
	  },
	}
	
</script>


<style lang="scss" scoped="scoped">
	.blogs{
		margin: 20px auto;
		max-width: 1000px;

		.lable{
			text-align: left;
			border-bottom: 1px solid #f56c6c;
			margin-bottom:1em; 
			.el-tag{
				margin: 1em;
				cursor: pointer;
			}
			.lablecolor{
				background-color: rgba(245,108,108,.1);
				border-color: rgba(245,108,108,.2);
				color: #f56c6c;
			}
		}
		
		.box{
			box-sizing: border-box;
			width: 33.3333%;
			float: left;
			cursor: pointer;
			.box-box{
				margin: 1em;
				overflow: hidden;
				border-radius: 5px;
				.box-lable{
						position: absolute;
						padding: 2px;
						background-color: #f56c6c;
						border-radius: 5px;
				}
				.box-img{
					max-width: 100%;
					height: auto;
					border-radius: 5px 5px  0px 0px;
					vertical-align:middle;
					-webkit-transition:-webkit-transform 1.5s; /* For Safari 3.1 to 6.0 */
					 transition:  transform 1.5s;
				}
				.box-text{
					background-color: #eee;
					font-size:1em; 
					line-height: 1.5em;
					text-align: center;
					border-radius: 0px 0px  5px 5px;
				}
				
			}

		}
		.box:hover .box-img{
			 -ms-transform: scale(2,2); /* IE 9 */
			-webkit-transform:scale(2,2);
			transform: scale(1.2,1.2);
		}
	}

</style>



