<template>
	<el-collapse-transition>
	<header class="nava">
		<el-menu
			:default-active="activeIndex"
			class="el-menu-demo"
			mode="horizontal"
			background-color="#fff"
			text-color="#455A64"
			active-text-color="#607D8B"
			:router="router"
			>
			<el-menu-item index="/" >技术分享</el-menu-item>
			<el-menu-item index="/d">留言</el-menu-item>
			<el-menu-item index="/about" >关于我</el-menu-item>
			
			<div class="title"><strong><router-link to="login" >路</router-link></strong>   <a href="http://houlai.fun"> 个人网站</a></div>
		</el-menu>
	</header>	
	</el-collapse-transition>
</template>
<script>
	export default {
	  name: 'navheader',
	  data () {
	    return {
				router:true,
	    }
	  },computed: {
	  	activeIndex() {
	  		return this.$route.path;
	  	}
	  },
	}
</script>

<style  lang="scss" scoped>

	.nava{
		box-shadow:0 2px 4px rgba(0,0,0,.12),0 0 6px rgba(0,0,0,.04);
		.el-menu-demo{
			max-width: 1024px;
			margin: 0 auto;
	
				.title{
					color: #795548;
					letter-spacing: 2px;
					float: right;
					height:60px;
						line-height: 60px;
						font-size: 1em;
						margin-right: 5%;
						a:hover{
							color: #455A64;
						}
						strong{
							color: #000;
							font-size: 2em;
							font-weight: 900;
						}
				}
			}
	}
</style>



